</template>

<script>
import sideBar from "@/components/sideBar.vue";
import topNav from "@/components/topNav.vue";
import axios from "axios"
export default {
  name: "editPoductoApp",
  components: {
    sideBar,
    topNav,
  },
  data(){
    return{
      str_img: '/assets/img/icon-ropa.jpg', 
      producto: {
        genero:'',
        categoria:'',
        estado: true,
        descuento: false,
        portada: undefined,
      },
      portada: undefined,
      variedad: {},
      variedades:[],
    }
  },
  methods:{
    init_data(){
        axios.get(this.$url+'/obtener_producto_admin/'+this.$route.params.id,{
            headers:{
                'Content-Type': 'application/json',
                'Authorization': this.$store.state.token
            }
        }).then((result)=>{
            this.producto = result.data
            this.str_img = this.$url+'/obtener_portada_producto/'+this.producto.portada;
        })
    },
      uploadImage($event){
        var img;
        if($event.target.files.length >= 1){
          img = $event.target.files[0];
        }
        if(img.size <= 1000000){
          if(img.type == 'image/jpg' || img.type == 'image/png' || img.type == 'image/webp' || img.type == 'image/jpeg' ){
            this.str_img = URL.createObjectURL(img);  
            this.portada = img;
            this.producto.portada = this.portada;        
          }else{
        this.$notify({
            title: 'ATENCI√ìN',
            text: 'Solo se admiten imagenes.',
            type: 'warn'
          });
          this.portada = undefined
        }
      } else{
          this.$notify({
            title: 'ATENCI√ìN',
            text: 'La imagen no debe ser mayor a 1mb.',
            type: 'warn'
          });
          this.portada = undefined
        }
    },
    validar(){
      if(!this.producto.nombre){
        this.$notify({
          title: 'ATENCI√ìN',
          text: 'Ingresa el nombre de producto',
          type: 'error',
        });
      }else if(!this.producto.clave){
        this.$notify({
          title: 'ATENCI√ìN',
          text: 'Ingresa la clave de producto',
          type: 'error',
        });
      }else if(!this.producto.genero){
        this.$notify({
          title: 'ATENCI√ìN',
          text: 'Selecciona el genero',
          type: 'error',
        });
      }else if(!this.producto.categoria){
        this.$notify({
          title: 'ATENCI√ìN',
          text: 'Selecciona una categoria',
          type: 'error',
        });
      }else if(!this.producto.descripcion){
        this.$notify({
          title: 'ATENCI√ìN',
          text: 'Agrega una descripcion del producto',
          type: 'error',
        });
      }else if(this.producto.portada == undefined){
        this.$notify({
          title: 'ATENCI√ìN',
          text: 'Ingresa la imagen de portada',
          type: 'error',
        });
      } else{
        this.actualizar()
      }
    },
    actualizar() {
  const formData = new FormData();
  
  // Agrega todos los campos obligatorios
  formData.append('nombre', this.producto.nombre);
  formData.append('clave', this.producto.clave);
  formData.append('genero', this.producto.genero);
  formData.append('categoria', this.producto.categoria);
  formData.append('descripcion', this.producto.descripcion);
  formData.append('estado', this.producto.estado);
  formData.append('descuento', this.producto.descuento);

  // Solo agrega la imagen si existe
  if (this.portada instanceof File) {
    formData.append('portada', this.portada);
  }

  axios.put(`${this.$url}/actualizar_producto_admin/${this.$route.params.id}`, formData, {
    headers: {
      'Content-Type': 'multipart/form-data',
      'Authorization': this.$store.state.token
    }
  }).then((result) => {
    this.$notify({
      title: '√âXITO',
      text: 'Producto actualizado correctamente',
      type: 'success'
    });
    this.$router.push({name:  'index-producto'});
  }).catch((error) => {
    console.error('Error detallado:', error.response?.data);
    this.$notify({
      title: 'ERROR',
      text: error.response?.data?.message || 'Error al actualizar el producto',
      type: 'error'
    });
  });
},
    validar_variedad(){
        if(!this.variedad.color){
            this.$notify({
                title: 'ATENCI√ìN',
                text: 'Agregar el color',
                type: 'warn'
            });
        } else if(!this.variedad.talla){
            this.$notify({
                title: 'ATENCI√ìN',
                text: 'Agregar la talla',
                type: 'warn'
            });
        } else {
            this.variedad.producto = this.$route.params.id;
            this.variedad.sku = this.generar_sku()
            this.registar_variedad()
        }
    },
    registar_variedad(){
      axios.post(this.$url+'/registro_variedad_producto',this.variedad,{
        headers: {
            'Content-Type': 'application/json',
            'Authorization': this.$store.state.token
        }
      }).then((result)=>{
        this.variedad = {};
        this.$notify({
            title: 'SUCCESS',
            text: 'Se agrego la variedad al producto',
            type:  'success'
        })
        this.init_variedades()
      })  
    },
    generar_sku(){
        let sku = (this.producto.clave+'-'+this.variedad.color.substring(0,3)+'-'+this.variedad.talla.substring(0,2)).toUpperCase();
        return sku
    },
    init_variedades(){
        axios.get(this.$url+'/obtener_variedades_producto/'+this.$route.params.id,{
            headers:{
                'Content-Type': 'application/json',
                'Authorization': this.$store.state.token,
            }
        }).then((result)=>{
            this.variedades = result.data;
        })
    },
    eliminar_variedad(id){
      axios.delete(this.$url+'/eliminar_variedades_producto/'+id,{
        headers:{
          'Content-Type': 'application/json',
          'Authorization': this.$store.state.token,
        }
      }).then((result)=>{
        if(result.data.message){
          this.$notify({
          title: 'ERROR',
          text: result.data.message,
          type: 'error'
        })
        }else{
          this.$notify({
          title: 'SUCCESS',
          text: 'Se elimino la variedad',
          type: 'success'
        })
        this.init_variedades()
        } 
      });
    },
},
  beforeMount(){
    this.init_data();
    this.init_variedades()
  },
}
</script>
